.simple-input {
	display: inline-block;
	vertical-align: top;
	padding: 0 15px;
	border: 1px solid $lightGray;
	width: 100%;
	height: 60px;
	color: $darkGray;
	font-size: 18px;
	font-weight: 400;
	line-height: normal;
	transition: border-color 0.17s ease-in-out;
	&:focus {
		border-color: $blue; } }
