.box {
	position: relative;
	padding: 49px 0 0;
	margin: 0 20px;
	border: 1px solid $lightGray;
	height: 100%;
	&__heading {
		position: absolute;
		top: -1px;
		left: -1px;
		right: -1px;
		padding: 13px 20px;
		color: $white;
		background: $blue;
		font-size: 22px;
		font-weight: 500;
		line-height: 24px;
		@include md {
			font-size: 18px; }
		@include sm {
			font-size: 16px; } }
	&__inner {
		overflow: auto;
		padding: 20px;
		height: 100%; } }
