* {
	font-family: 'Roboto', Arial, sans-serif; }
html,
body,
.main {
	position: relative;
	height: 100%; }

body {
	min-width: 600px;
	background: $white;
	font-size: 14px;
	line-height: 1.4; }

.main {
	padding-top: 120px;
	padding-bottom: 100px;
	margin: 0 auto;
	max-width: 1200px; }


//text mods
.text-uppercase {
	text-transform: uppercase; }

.color-red {
	color: $red; }
